.footer {
  background-color: var(--bg-dark);
  color: var(--text-light);
  padding: 60px 0 20px;
  margin-top: 60px;
}

.footer-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 40px;
}

.footer-section h3 {
  margin-bottom: 20px;
  color: var(--accent);
}

.footer-links {
  list-style: none;
}

.footer-link {
  color: var(--text-light);
  text-decoration: none;
  margin-bottom: 10px;
  display: block;
  transition: var(--transition);
}

.footer-link:hover {
  color: var(--accent);
}

.social-links {
  display: flex;
  gap: 15px;
  margin-top: 20px;
}

.copyright{
margin: 0;
text-align: center;
}

.icons{
  color:white;
}

.icons:hover{
  color: var(--primary);
}